

























import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import FormItem from '@/components/money/FormItem.vue';

@Component({
  components:{FormItem}
})
export default class Types extends Vue {
  output = '0'
  value=''
  @Watch('output')
  onNumberChanged(){
    this.$emit('numberChanged', this.output)
  }
  @Watch('value')
  onValueChanged(){
    this.$emit('ValueChanged',this.value)
  }

  changeValue(value: string){
    this.value = value
  }

  inputContent(event: MouseEvent) {
    const button = (event.target as HTMLButtonElement);
    const text = button.innerText;
    if (this.output.length === 16) {
      alert('最多16位数字');
      return;
    }
    if (this.output === '0') {
      if (text === '0') {
        return;
      } else if (text === '.') {
        this.output = '0.';
        return;
      }else if('123456789'.indexOf(text) >=0 ){
        this.output = text
        return
      }
    }
    if (this.output.includes('.') && text === '.') {
      return;
    }
    if (this.output.includes('.') && (this.output.length - this.output.lastIndexOf('.') > 2)) {
      return;
    }
    this.output += text;
  }

  clear() {
    this.output = '0';
  }

  ok() {
    this.$emit('update', this.output);
    this.$emit('save', this.output);
    this.clear();
  }

  remove() {
    if (this.output.length === 1) {
      this.output = '0';
    } else {
      this.output = this.output.slice(0, -1);
    }
  }


}
